import avatar from "../../components/yq-avatar/yq-avatar.vue";
export default {
	components: {
		avatar
	},
	data() {
		return {
			index: 1,
			textInput: '',
			colorId: 0,
			url: "../../static/horse.png",
			idd: 1,
			width: '120px',
			height: '30px',
			title: '表情加字',
			tip: '选择文字样式',
			boll: [{
				size: '小',
				bc: '透明',
				id: 1
			}, {
				size: '中',
				bc: '白',
				id: 2
			}, {
				size: '大',
				bc: '黑',
				id: 3
			}],
			flag: false,
			txt: '',
			styleCss: {
				top: Math.random() * 100 + 'px',
				left: Math.random() * 150 + 'px',
				font: '15px',
				color: '#000',
				bcColor: '#ffffff'
			},
			startx: 0,
			starty: 0,
			disX: 0,
			disY: 0
		}
	},
	onLoad() {
		this.flag = true
	},
	methods: {
		myUpload(rsp) {
			this.url = rsp.path; //更新头像方式一
			//rsp.avatar.imgSrc = rsp.path; //更新头像方式二
		},
		handleKeUp(e) {
			if (e.detail.value.length === 0) {
				e.detail.value = '     '
			}
			this.textInput = e.target.value
			let i = this.idd
			this.len = e.detail.value.length
			this.changeInput(i, this.len)
			console.log(this.len)
		},
		changeInput(i, len) {
			if (i === 1) {
				var fontone = len * 17 + 'px'
				this.width = fontone
				this.height = '30px'
			} else if (i === 2) {
				var fontone = len * 32 + 'px'
				this.width = fontone
				this.height = '45px'
			} else if (i === 3) {
				var fontone = len * 47 + 'px'
				this.width = fontone
				this.height = '65px'
			}
		},
		changeColor(e) {
			var id = e.currentTarget.dataset.id
			id = JSON.parse(id)
			switch (id) {
				case 1:
					this.styleCss.color = '#f40'
					break;
				case 2:
					this.styleCss.color = '#F40'
					break;
				case 3:
					this.styleCss.color = 'yellow'
					break;
				case 4:
					this.styleCss.color = '#0C0'
					break;
				case 5:
					this.styleCss.color = '#699'
					break;
				case 6:
					this.styleCss.color = '#06C'
					break;
				case 7:
					this.styleCss.color = '#909'
					break;
				case 8:
					this.styleCss.color = '#fff'
					break;
				case 9:
					this.styleCss.color = '#455'
					break;
				case 10:
					this.styleCss.color = '#ccc'
					break;
				case 11:
					this.styleCss.color = '#555'
					break;
				case 12:
					this.styleCss.color = '#777'
					break;
				default:
					this.styleCss.color = '#999'
			}
		},
		start(e) {

			this.startx = e.changedTouches[0].clientX - e.currentTarget.offsetLeft
			this.starty = e.changedTouches[0].clientY - e.currentTarget.offsetTop
		},
		move(e) {
			this.disX = e.changedTouches[0].clientX - this.startx
			this.disY = e.changedTouches[0].clientY - this.starty
			this.styleCss.top = this.disY + 'px'
			this.styleCss.left = this.disX + 'px'
		},
		end(e) {},
		losefocurs(e) {
			this.handleKeUp(e)
		},
		handSizeClick(e) {
			this.key = true
			var id = e.currentTarget.dataset.id
			this.idd = id
			if (id === 1) {
				this.styleCss.font = '15px'
				this.height = '30px'
			} else if (id === 2) {
				this.styleCss.font = '30px'
				this.height = '45px'
			} else if (id === 3) {
				this.styleCss.font = '45px'
				this.height = '65px'
			}
			this.changeInput(this.idd, this.len)

		},
		handColorClick(e) {
			var id = e.currentTarget.dataset.id
			this.colorId = id
			if (id === 1) {
				this.styleCss.bcColor = 'transparent'
			} else if (id === 2) {
				this.styleCss.bcColor = '#fff'
			} else if (id === 3) {
				this.styleCss.bcColor = '#000'
			}
		},
		handleCloseClick() {
			this.flag = !this.flag
		},
		handleNewFace() {
			this.index = 0
		},
		addText() {
			this.flag = !this.flag
		}
	}
}
